import React from 'react';

import icons from '../../svg/icons.svg';

import './Icon.scss';

const Icon = ({ name, className }) => {
  return (
    <svg className={ className ? `icon-${name} ${className}` : `icon-${name}`}>
      <use 
        href={`${icons}#icon-${name}`}
      />
    </svg>
  );
};

export default Icon;