import React, { useState, useEffect } from 'react';
import { Switch, Route } from 'react-router-dom';

import Header from './components/Header/Header';
import NavBar from './components/NavBar/NavBar';

import About from  './pages/About/About';
import Projects from './pages/Projects/Projects';
import Contact from './pages/Contact/Contact';
import Skills from './pages/Skills/Skills';

import './App.css';

const App = () => {
  const checkPreviousValue = (window.localStorage.getItem('isNightMode') === "true");
  const [isNightMode, setNightMode] = useState(checkPreviousValue || false);

  if (isNightMode) {
    document.body.classList.add('night-mode');
  }

  const toggleNightMode = () => {
    setNightMode(!isNightMode)
  };

  useEffect(() => {
    document.body.classList.toggle('night-mode');
    window.localStorage.setItem('isNightMode', isNightMode);
  }, [isNightMode]);

  return (
    <>
      <Header toggleNightMode={toggleNightMode} />
      <NavBar />
      <Switch>
        <Route exact path='/' component={About}/>
        <Route exact path='/projects' component={Projects}/>
        <Route exact path='/skills' component={Skills} />
        <Route exact path='/contact' component={Contact} />
      </Switch>
    </>
  );
};

export default App;
